import React from "react"
class InstructionTemplate extends(React.Component){
    constructor(props){
        super(props)
        this.nextInstruction=this.nextInstruction.bind(this)
        this.key_press=this.key_press.bind(this)
    }
    componentDidMount(){
        document.addEventListener("keydown",this.key_press)
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this.key_press)
    }
    key_press(event){
        if (event.key===this.props.nextInstructionKey){
            this.nextInstruction()
        }
        if(event.key===this.props.previousInstructionKey){
            this.previousInstruction()
        }
    }
    nextInstruction(){
        this.props.nextInstruction()
    }
    previousInstruction(){
        this.props.previousInstruction()
    }
    render(){
    var divs=[]
    var keys=[]
    for (var i=0;i<this.props.divs.length;i++){
        divs.push(this.props.divs[i])
    }
    if (this.props.previousInstructionKey){
        keys.push(<div style={{ width:"200px", height:"50px", border:"solid black 5px", textAlign:"center", fontSize:"20px", margin:"5px"}}>{`Press ${this.props.previousInstructionKey} to go back`}</div>
        )
    }
    if (this.props.nextInstructionKey){
        keys.push(<div style={{ width:"200px", height:"50px", border:"solid black 5px", textAlign:"center", fontSize:"20px", margin:"5px"}}>{`Press ${this.props.nextInstructionKey} to continue`}</div>
        )
    }
    return (
    <div>
        {divs}
    <div style={{display:"flex",flex:"row", position:"fixed", justifyContent:"space-evenly", bottom:"5%", width:"100%"}}>
        {keys}
    </div>
    </div>)
}}
export default InstructionTemplate