import React from "react"
import "../style.css"
class Lever extends(React.Component){
    constructor(props){
        super(props)
        this.delayAnimation=this.delayAnimation.bind(this)
        if (props.delayAnimation){
            this.delayAnimation(this.props.timeDelay)
        }
        this.state={animate:this.props.animate}
    }
    delayAnimation(timeDelay){
        setTimeout(()=>{
            this.setState({animate:true})
        }, timeDelay)
    }
    render(){
        return <div>
                    <div className="lever_ball" style={{width:"25px",height:"25px", borderRadius:"50%", backgroundColor:this.props.backgroundColor, animation:(this.state.animate ? "ball_clink":""), animationDuration:(this.state.animate ? ".5s":"")}}>
                    </div>
                    <div className="lever_arm" style={{width:"5px",height:"90px", backgroundColor:this.props.backgroundColor, animation:(this.state.animate ? "arm_clink": ""), animationDuration:(this.state.animate ? ".5s":"")}}>
                    </div >
                    <div style={{position: "relative", width:25, height:5, backgroundColor:this.props.backgroundColor, top: "25%",left:"0px"}}>
                    </div>
                </div>
    }
}

export default Lever