import React from "react"
import GameMachine from "./GameMachine"
import KeyDisplay from "./KeyDisplay"
import Lever from "./Lever"
import PointCounter from "./PointCounter"
import TotalPointCounter from "./TotalPointCounter"
import "../style.css"
class HorizonTrial extends(React.Component){
    //props={gameLength:x,machineVars:[{mu:x,sd:x,keyBind:a,color:x},{mu:x,sd:x,keyBind:b,color:y}],force=[]}
    constructor(props){
        super(props)
        this.subtrial=-1
        this.key_map=get_keys(this.props.machineVars)
        this.column_map=convert_to_column_map(this.props.machineVars)
        this.key_press =this.key_press.bind(this)
        this.advance_subtrial=this.advance_subtrial.bind(this)
        this.process_choice=this.process_choice.bind(this)
        this.next_trial=this.next_trial.bind(this)
        this.data={meta_data:this.props,trial_data:[]}
        this.state={}
        console.log(this.column_map)
        this.advance_subtrial()
        console.log(this.column_map)
        for (var i =0; i<this.props.machineVars.length; i++){
            this.state[String(i)]={valid:this.column_map[String(i)].valid,discovered:[],description:this.column_map[String(i).description]}
        }
        this.state["total_points"]=props.total_points
        this.state["round_points"]=0
    }
    componentDidMount(){
        document.addEventListener("keydown",this.key_press)
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this.key_press)
    }
    key_press(event){
        //gives the column number mapping to the keypress
        if (String(event.key) in this.key_map){
            if (this.column_map[String(this.key_map[event.key])].valid){
                this.process_choice(String(this.key_map[String(event.key)]))
            }
        }
    }
    process_choice(column){
        this.advance_subtrial()
        //takes column and updates the discovery
        let new_state={}
        let new_value=null
        for (var i=0;i<this.props.machineVars.length;i++){
            if (String(i)===column){
                new_value=get_random(this.column_map,column)
                this.column_map[String(i)].discovered.push(new_value)
            }else{
                this.column_map[String(i)].discovered.push("xxx")
            }
            new_state[String(i)]={valid:this.column_map[String(i)].valid,discovered:this.column_map[String(i)].discovered}   
        }
        new_state["last_choice"]=column
        console.log("force size: "+String(this.props.force.length))
        if (this.subtrial>this.props.force.length){
            new_state["total_points"]=this.state["total_points"]+new_value
            new_state["round_points"]=this.state["round_points"]+new_value
        }

        this.data.trial_data.push({choice: column, value: new_value})
        this.setState(new_state)
        if (this.subtrial>=this.props.gameLength){
            setTimeout(this.next_trial,1000)
            console.log("hi")
        }else{
        }
        console.log(this.data)
    }
    next_trial(){
        console.log("TRIAL DONE!")
        //console.log(this.props.nextTrial)
        this.props.nextTrial(this.data,this.state.round_points)
    }
    advance_subtrial(){
        //moves the subtrial up by one, adds data, and determines which columns are valid choices for next time
        this.subtrial+=1
        console.log("trial: "+String(this.subtrial))
        if (this.subtrial<this.props.force.length){
            for (var i=0; i<this.props.machineVars.length; i++){
                if (this.props.force[this.subtrial].includes(i)){
                    this.column_map[String(i)].valid=true
                }
                else{
                    this.column_map[String(i)].valid=false
                }
            }
        }else{
            for (var j=0; j<this.props.machineVars.length; j++){
                this.column_map[String(j)].valid=true
            }
        }
    }
    render(){
        //displays the machines and advances the trial
        console.log(this.state)
        var items=[]
        var num = null
        var to_animate=false
        for (var i=0;i<this.props.machineVars.length;i++){
            num=this.state[String(i)].discovered.length
            console.log(num)
            if (String(i)==this.state["last_choice"]){
                to_animate=true
                console.log(i)
            }else{
                to_animate=false
            }
            items.push(
            <div className="column">
                <h2 style={{position:"relative", left:"10px"}}>
                    {this.column_map[String(i)].description}
                </h2>
                <div className="row">
                    <Lever key={Math.round(Math.random()*10000)} animate={to_animate} backgroundColor={this.column_map[String(i)].machineColor}/>
                    <GameMachine key={i} index={num} valid={this.state[String(i)].valid} values={this.state[String(i)].discovered} gameLength={this.props.gameLength} machineColor={this.column_map[String(i)].machineColor}/>
                </div>
                <KeyDisplay borderColor={this.column_map[String(i)].machineColor} machineKey={this.props.machineVars[i].keyBind}/>
            </div>
            )
        }
        return(<div>
                    <h1 style={{ width: "100%",textAlign:"center", display:"flex", flexDirection:"row", justifyContent:"space-between"}}>
                        <PointCounter points={this.state.round_points} width="300px" height="75px" borderColor="black" description="Money This Round"/>
                        {this.props.trialDescription}
                        <TotalPointCounter points={this.state.total_points} width="300px" height="75px" borderColor="black" description="Total Money Earned" total_points="20000"/>
                    </h1>
                    <div className="row" style ={{ justifyContent:"space-evenly",alignItems:"center"}}>{items}
                    </div>
                </div>)
    }
}
function get_keys(arr){
    //takes in the machine array, finds the keyBinds, and maps it to the column id.
    let vars={}
    for (var i=0;i<arr.length;i++){
        vars[String(arr[i].keyBind)]=i
    }
    return vars
}
function convert_to_column_map(arr){
    //takes in the machine array, and maps it into a column format with all the necessary variables
    let column_map={}
    for (var i=0;i<arr.length;i++){
        column_map[String(i)]={mu:arr[i].mu, sd:arr[i].sd, valid: false, discovered:[], machineColor:arr[i].color, description:arr[i].description}
    }
    return column_map
}
function get_random(arr,i){
    //retrieves random variable in accordance with the specified column number parameters
    //placeholder
    //let mu=arr[i].mu
    //let sd=arr[i].sd
    return Math.round(Math.random()*100)
}
export default HorizonTrial