import React from "react"
class BoxDiv extends(React.Component){
    constructor(props){
        super(props)
    }

    render(){
        return <div style={{
            borderWidth: "25px",
            border: "solid black",
            margin: "5px",
            backgroundColor:(this.props.borderColor ? this.props.borderColor: "white"),
            width: "40px", 
            height:"40px",
            alignItems:"center",justifyContent:"center",display:"flex"}}>
        {this.props.heldValue}</div>
    }
}
export default BoxDiv