import React from "react"
import BoxDiv from "./BoxDiv"
class GameMachine extends(React.Component){
    //pass in validIndex, values, gameLength
    //pass in valid, and index instead of validIndex b/c this is causing issues with null
    constructor(props){
        super(props)
        this.delayedNumber=this.delayedNumber.bind(this)
        this.state={index:this.props.index-1}
        if (props.delayNumberReveal){
            this.delayedNumber(this.props.timeDelay)
        }
    }
    delayedNumber(timeDelay){
        console.log(this.state)
        setTimeout(()=>{this.setState({index:this.state.index+1})},timeDelay)
    }
    render(){
        this.state.index+=1
        console.log("valid: "+String(this.props.valid))
        var gameBoxes=[]
        for(var i=0;i<this.props.gameLength;i++){
            let passedVars={}
            if (i<this.state.index){
                passedVars.heldValue=this.props.values[i]
            }
            if (i===this.state.index && this.props.valid){
                passedVars.borderColor="lightgreen"
            }
                gameBoxes.push(<BoxDiv key= {i} borderColor={passedVars.borderColor} heldValue={passedVars.heldValue}/>)
        }
        return(
        <div style={{border:`solid ${(this.props.machineColor)?this.props.machineColor:"black"} 5px`}}>
            {gameBoxes}
        </div>)
    }
}
export default GameMachine