import React from "react"
class PointCounter extends(React.Component){
    constructor(props){
        super(props)
    }
    render(){
        return (<div style={{width:this.props.width,height:this.props.height,border:`solid ${this.props.borderColor} 2px`}}>{this.props.description+":"}<div style={{color:"green"}}>{this.props.points+"$"}</div></div>)
    }
}
export default PointCounter