import React from "react"
class KeyDisplay extends(React.Component){
    constructor(props){
        super(props)
    }
    render(){
        return (<div style={{position: "relative", left: "10px", margin:"5px",padding:"5px", border:`solid ${this.props.borderColor} 2px`}}> Key: {this.props.machineKey}</div>)
    }
}
export default KeyDisplay