import React from "react"
class TotalPointCounter extends(React.Component){
    constructor(props){
        super(props)
    }
    render(){
        return (<div style={{ width:this.props.width,height:this.props.height,border:`solid ${this.props.borderColor} 2px`}}>{this.props.description+":"}<div style={{display:"flex", flex:"row", justifyContent:"center"}}><div style={{color:"green"}}>{this.props.points+"$"}</div><div>/</div><div>{this.props.total_points+"$"}</div></div></div>)
    }
}
export default TotalPointCounter